@charset "UTF-8";

.contianer{
  position: relative;
  width: 100%;
  display: grid;
  grid-row-gap: 80px;
  grid-column-gap: 50px;
  grid-template-columns: repeat(auto-fit, minmax(600px, 1fr));
  padding: 50px;
}

.items{
  display: flex;
  padding: 50px 30px 50px 50px ;
  background: #222;
  box-shadow: 0 10px 20px rgba(255, 255, 255, .3);
}

.load{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.items img{  height: 80%; transform: translateY(-80px); box-shadow: 0 10px 20px rgba(255, 255, 255, .3); }
.content{padding: 30px;}
.content h2{ padding: 20px 0; word-break: keep-all;}
.content h2 a{color: #fff;}
.content p{ margin: 20px 0; padding: 0 20px;  height: 200px; overflow-y: scroll; text-align: left; }
.content p::-webkit-scrollbar{
  width: 3px;
}
.content p::-webkit-scrollbar-thumb,
.content p::-webkit-scrollbar-track{
  border-radius: 10px;
}
.content p::-webkit-scrollbar-thumb{background: #fff;}
.content p::-webkit-scrollbar-track{background: transparent;}
.content ul{display: flex; }
.content ul li{padding: 0 10px;}